import { useCallback } from "react";
import { SortingOrder } from "../../@types/types";
import { SetSortQueryInURL } from "./useSortingOrder";

const useSortingFunctions = (
  sortingOrder: SortingOrder,
  setSortQueryInURL: SetSortQueryInURL
) => {
  const setSortingOrderByCurrencyName = useCallback(() => {
    const newSortQueryInUrlValue =
      sortingOrder === "SORT_BY_CURRENCY_NAME_DESCENDING"
        ? "currency-asc"
        : "currency-desc";

    setSortQueryInURL(newSortQueryInUrlValue);
  }, [sortingOrder, setSortQueryInURL]);

  const setSortingOrderByAverageExchangeRate = useCallback(() => {
    const newSortQueryInUrlValue =
      sortingOrder === "SORT_BY_AVERAGE_EXCHANGE_RATE_DESCENDING"
        ? "rate-asc"
        : "rate-desc";

    setSortQueryInURL(newSortQueryInUrlValue);
  }, [sortingOrder, setSortQueryInURL]);

  return [
    setSortingOrderByCurrencyName,
    setSortingOrderByAverageExchangeRate,
  ] as const;
};

export default useSortingFunctions;
