import { useCallback } from "react";
import { useSearchParams } from "react-router-dom";
import { SortingOrder } from "../../@types/types";

export type SortQueryInURL =
  | "currency-asc"
  | "currency-desc"
  | "rate-asc"
  | "rate-desc"
  | null;

const SORT_QUERY_IN_URL_KEY_NAME = "sort";

export interface SetSortQueryInURL {
  (newQueryValue: SortQueryInURL): void;
}

const useSortingOrder = () => {
  const [searchParams, setSearchParams] = useSearchParams();

  const sortQueryInURL: SortQueryInURL = searchParams.get(
    SORT_QUERY_IN_URL_KEY_NAME
  ) as SortQueryInURL;

  let sortingOrder: SortingOrder = "NONE";

  switch (sortQueryInURL) {
    case "currency-asc":
      sortingOrder = "SORT_BY_CURRENCY_NAME_ASCENDING";
      break;
    case "currency-desc":
      sortingOrder = "SORT_BY_CURRENCY_NAME_DESCENDING";
      break;
    case "rate-asc":
      sortingOrder = "SORT_BY_AVERAGE_EXCHANGE_RATE_ASCENDING";
      break;
    case "rate-desc":
      sortingOrder = "SORT_BY_AVERAGE_EXCHANGE_RATE_DESCENDING";
      break;
    default:
      break;
  }

  const setSortQueryInURL: SetSortQueryInURL = useCallback(
    (newQueryValue: SortQueryInURL) => {
      const newSearchParamsInstance = new URLSearchParams(searchParams);

      if (!newQueryValue) {
        newSearchParamsInstance.delete(SORT_QUERY_IN_URL_KEY_NAME);
      } else {
        newSearchParamsInstance.set(SORT_QUERY_IN_URL_KEY_NAME, newQueryValue);
      }

      setSearchParams(newSearchParamsInstance);
    },
    [searchParams, setSearchParams]
  );

  return [sortingOrder, setSortQueryInURL] as const;
};

export default useSortingOrder;
