import { useState } from "react";
import { ActionStatus } from "./@types/types";

import useFetchCurrenciesExchangeRateFromApi from "./components/hooks/useFetchCurrenciesExchangeRateFromApi";
import { ErrorIndicator } from "./components/ErrorIndicator";

import { LoadingIndicator } from "./components/LoadingIndicator";
import TableCurrencyExchangeRate from "./components/TableCurrencyExchangeRate";

function App() {
  const [fetchingDataOperationStatus, setFetchingDataOperationStatus] =
    useState<ActionStatus>("PROCESSING");

  const currenciesExchangeRateFromApi = useFetchCurrenciesExchangeRateFromApi(
    setFetchingDataOperationStatus
  );

  if (fetchingDataOperationStatus === "FAILED") {
    return <ErrorIndicator />;
  } else if (fetchingDataOperationStatus === "PROCESSING") {
    return <LoadingIndicator />;
  } else {
    return (
      <TableCurrencyExchangeRate
        currenciesExchangeRateFromApi={currenciesExchangeRateFromApi}
      />
    );
  }
}

export default App;
