import { TableCell, TableRow, Typography } from "@mui/material";
import { SortingOrder } from "../@types/types";

import SortableTableHeaderCell from "./SortableTableHeaderCell";

interface Props {
  sortingOrder: SortingOrder;
  setSortingOrderByCurrencyName: () => void;
  setSortingOrderByAverageExchangeRate: () => void;
}

const TableHeaderCurrencyExchangeRate = ({
  sortingOrder,
  setSortingOrderByCurrencyName,
  setSortingOrderByAverageExchangeRate,
}: Props) => {
  return (
    <TableRow>
      <SortableTableHeaderCell
        sortingOrder={
          sortingOrder === "SORT_BY_CURRENCY_NAME_ASCENDING"
            ? "ASCENDING"
            : sortingOrder === "SORT_BY_CURRENCY_NAME_DESCENDING"
            ? "DESCENDING"
            : "NONE"
        }
        sortingFunction={setSortingOrderByCurrencyName}
        headerText="Nazwa waluty"
        width="40%"
      />

      <TableCell align="right" sx={{ width: "30%" }}>
        <Typography sx={{ fontSize: "inherit" }}>Kod waluty</Typography>
      </TableCell>
      <SortableTableHeaderCell
        sortingOrder={
          sortingOrder === "SORT_BY_AVERAGE_EXCHANGE_RATE_ASCENDING"
            ? "ASCENDING"
            : sortingOrder === "SORT_BY_AVERAGE_EXCHANGE_RATE_DESCENDING"
            ? "DESCENDING"
            : "NONE"
        }
        sortingFunction={setSortingOrderByAverageExchangeRate}
        headerText="Kurs redni(g)"
        width="30%"
        justifyContent="end"
      />
    </TableRow>
  );
};

export default TableHeaderCurrencyExchangeRate;
